import React, { useEffect } from 'react';
import { useInView } from "react-intersection-observer"
import { motion, useAnimation } from "framer-motion"

export default function ImageToRight({ title, description, image }) {
  const controls = useAnimation()
  const [ref, inView] = useInView({
    // Percentage of item in view to trigger animation
    threshold: 0.25,
  })

  useEffect(() => {
    if (inView) {
      controls.start("visible")
    }
  }, [controls, inView])

  return (
    <div className="flex flex-col mb-10 max-w-screen-lg mx-auto py-5 md:flex-row">
      <motion.div
        ref={ref}
        animate={controls}
        initial="hidden"
        variants={{
          visible: { opacity: 1, y: 0 },
          hidden: { opacity: 0, y: 25 },
        }}
        transition={{ ease: "easeOut", duration: 1.25, delay: 0.35 }}
        className="flex-1 mb-10 text-center md:mb-0"
      >
        <h2 className="section-title px-8">{title}</h2>
        <p className="px-8">{description}</p>
      </motion.div>

      <motion.img
        ref={ref}
        animate={controls}
        initial="hidden"
        variants={{
          visible: { opacity: 1, y: 0 },
          hidden: { opacity: 0, y: 25 },
        }}
        transition={{ ease: "easeOut", duration: 1.25, delay: 0.35 }}
        className="flex-1 my-auto mx-auto"
        src={image}
        alt={title}
      />
    </div>
  );
}